/* Basic Input Styling */
input[type="text"] {
  padding: 10px;
  border: none;
  border-bottom: 2px solid #007bff; /* Change this color to your preference */
  background-color: transparent;
  width: 50%;
  font-size: 16px;
  color: #333;
  transition: border-bottom-color 0.3s ease;
}

/* Hover Effect */
input[type="text"]:hover {
  border-bottom-color: #0056b3; /* Change this color to your preference */
}

/* Focus Effect */
input[type="text"]:focus {
  border-bottom-color: #0056b3; /* Change this color to your preference */
  outline: none;
}

/* Error State */
input[type="text"].error {
  border-bottom-color: #dc3545;
}

/* Disabled State */
input[type="text"]:disabled {
  color: #6c757d;
  border-bottom-color: #f8f9fa;
}
